import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import '../styles/Proyectos/Momentos/MomentosPC.css';
import '../styles/Proyectos/Momentos/MomentosMOBILE.css';

function Momentos() {
  // Estado para controlar la imagen actual
  const [currentSlide, setCurrentSlide] = useState(0);

  // Lista de imágenes del carrusel
  const images = [
    'img/MUI1.jpg',
    'img/MUI2.jpg',
    'img/MUI3.jpg',
    'img/MUI4.jpg'
  ];

  // Función para avanzar al siguiente slide
  const nextSlide = () => {
    setCurrentSlide((prevSlide) => (prevSlide + 1) % images.length);
  };

  // Función para retroceder al slide anterior
  const prevSlide = () => {
    setCurrentSlide((prevSlide) =>
      prevSlide === 0 ? images.length - 1 : prevSlide - 1
    );
  };

  return (
    <div className="momentos-container">
      <h1>Momentos Project</h1>
      
      {/* Imagen de laptop con contenido detrás */}
      <div className="laptop-frame">
        <div className="carousel-container">
          <div className="carousel-slide">
            {/* Imagen del carrusel */}
            <img src={images[currentSlide]} alt={`Imagen ${currentSlide + 1}`} />
          </div>
          {/* Botones de control del carrusel */}
          <button className="carousel-control prev" onClick={prevSlide}>
            &#10094;
          </button>
          <button className="carousel-control next" onClick={nextSlide}>
            &#10095;
          </button>
        </div>
        <img className="laptop-image" src="img/laptop.png" alt="Laptop" />
      </div>

      <p>
        Proyecto Momentos es una página web diseñada para la empresa de fotografía Momentos, 
        donde los usuarios pueden registrarse y acceder a una amplia variedad de servicios fotográficos. <br />
        A través de esta plataforma, los clientes pueden explorar diferentes paquetes para sesiones fotográficas, 
        adaptados a diversas necesidades como bodas, eventos especiales, retratos familiares y más.
      </p>
    
      {/* Enlace al repositorio de GitHub con imagen */}
      <a href="https://github.com/SaithG04/Web_Site_Momentos" className="github-link" target="_blank" rel="noopener noreferrer">
        <img src="img/github_icon.png" alt="GitHub" className="github-icon" />
      </a>

      <Link to="/Portafolio-ThonyMarckDEV" className="btn">Volver al Inicio</Link>
    </div>
  );
}

export default Momentos;
